.container{
    justify-content: center;
   align-items: center;
   position: relative;
   padding: 0 2rem;
   display: flex;
   flex-direction: column;
   gap: 2rem;
}
.container>:nth-child(1){
    position: absolute;
    width: 8rem;
    top: -5rem;
    left: 25%;
}
.container>:nth-child(2){
font-size: 2rem;
font-weight: 800;
}
.products{
    display: grid;
    width:90%;
    grid-template-columns: 25% auto; 
}
.product{
    background: white;
    height: 9rem;
    display: flex;
    border-radius: 10px;
    padding: 1rem;
    box-sizing: border-box;
    position: relative;
    gap: 2rem;
    width: 13rem;
    overflow: hidden;
}

.name>:nth-child(1){
    font-size: 1.4rem;
    font-weight: 600;
    display: flex;
    }

    .name>:nth-child(2){
        font-size: .8rem;
        }

