    .imgp{
        transform: rotate(-20deg);
        position: absolute;
        right: 0;
        width: 30%;
        height: 100%;
        bottom: -30%;
        }
   .menu{
            list-style: none;
            display: flex;
            flex-direction: column;
            gap: 2rem;
            font-weight: 500;
            font-size: 1.3rem
        }
        .menu li:hover{
            color: var(--pink);
            cursor: pointer;
        }
        .list{
            display: grid;
            grid-template-columns: repeat(auto-fill, minmax(200px,1fr));
            height: 25rem;
            overflow-y: scroll;
            gap: 2rem;
        }
       
        @media (max-width:800px){
            .menu{
                display: flex;
           flex-direction: row;
   position: relative;
   left: -60px;
        }
            .list{
                margin-top: 50%;
                display: grid;
                grid-template-columns: repeat(auto-fill, minmax(200px,1fr));
            }
                  }